* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#main {
  padding: 1rem 1rem 0.5rem 1rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  margin-left: -0.5rem;
  margin-right: -0.5rem;
}

.links {
  padding: 1rem 1rem 0.5rem 1rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  margin-left: -0.5rem;
  margin-right: -0.5rem;
  border: 2px solid #fc0;
  text-align: center;
}
a:hover {
  text-decoration: underline;
}

a {
  text-decoration: none;
  color: black;
}

.container {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 1.5;
  max-width: 35rem;
  padding-left: 1rem;
  padding-right: 1rem;

position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}